@import url(https://fonts.googleapis.com/css2?family=Fira+Sans:wght@400;500;600;700&display=swap);
:root {
    --dark: #333;
    --dark-light: #404040;
    --light: whitesmoke;
    --primary: cornflowerblue;
    --secondary: #f0a253;
    --error: #a63333;
    --fira-sans: 'Fira Sans', sans-serif;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

#root {
    width: 1280px;
    margin-top: 72px;
    padding: 24px;
}

body {
    display: flex;
    justify-content: center;
    background-color: #333;
    background-color: var(--dark);
    color: whitesmoke;
    color: var(--light);
    font-family: 'Fira Sans', sans-serif;
    font-family: var(--fira-sans);
    font-weight: 400;
    min-height: 100vh;
}

h1 {
    font-size: 40px;
    text-align: center;
    margin: 24px 0;
    text-transform: uppercase;
    color: #f0a253;
    color: var(--secondary);
}

p {
    padding-bottom: 16px;
}

input {
    font-family: 'Fira Sans', sans-serif;
    font-family: var(--fira-sans);
    border: none;
    border-bottom: 2px solid whitesmoke;
    border-bottom: 2px solid var(--light);
    background-color: transparent;
    color: whitesmoke;
    color: var(--light);
    font-size: 16px;
    outline: none;
    padding: 8px 0;
}

input:hover, .input:focus {
    border-color: #f0a253;
    border-color: var(--secondary);
}

input:disabled {
    cursor: wait;
}

input:-webkit-autofill {
    -webkit-box-shadow: 0 0 0 1000px #333 inset;
    -webkit-box-shadow: 0 0 0 1000px var(--dark) inset;
    -webkit-text-fill-color: whitesmoke !important;
    -webkit-text-fill-color: var(--light) !important;
}

a {
    color: cornflowerblue;
}
